import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {WimaxServiceProvider} from "../../providers/wimax-service/wimax-service";
import {TokenServiceProvider} from "../../providers/token-service/token-service";


@IonicPage()
@Component({
  selector: 'page-wimax-recharge02',
  templateUrl: 'wimax-recharge02.html',
})
export class WimaxRecharge02Page {

  accountNo;
  wimaxRechargeError02;
  packageType;
  provider;
  orgProvider;
  sProvider;
  pType;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public maskTokenUtil: MaskTokenProvider,
              public storage: LocalStorageProvider,
              public alertCtrl: AlertController,
              public cryptoUtil : CryptoParamUtilProvider,
              public wimaxService: WimaxServiceProvider,
              public tokenService : TokenServiceProvider) {

    this.accountNo=this.navParams.get("accountNo");
  }

  WimaxRecharge02Action = function (provider, packageType) {
    this.wimaxRechargeError02 = "";

    let flag = true;

    if (provider == "" || typeof(provider) == "undefined")
    {
      this.wimaxRechargeError02 = "Please Select Service Provider";
      flag = false;
      return;
    }

    if (packageType == "" || typeof(packageType) == "undefined")
    {
      this.wimaxRechargeError02 = "Please Select Package.";
      flag = false;
      return;
    }


    if (flag)
    {
      this.orgProvider=provider;
      this.sProvider=provider.toLowerCase();
      this.pType=packageType;

      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encWimaxProvider = this.cryptoUtil.getEncryptVal(this.sProvider);
      let encPackage = this.cryptoUtil.getEncryptVal(this.pType);
      let encAccount = this.cryptoUtil.getEncryptVal(this.accountNo);
      let unMaskToken=this.maskTokenUtil.getUnMaskVal(appData.token);
      this.wimaxService.fetchOrderDetails(unMaskToken, appData.userId, appData.appId, encWimaxProvider,encPackage,encAccount)
        .then(data => {
          this.onOrderDetailFetchComplete(data);
        });
    }
  };

  onOrderDetailFetchComplete = function (data) {

    if (data.statusCode == 1) {
      this.navCtrl.push('WimaxRecharge03Page',{
        'accountNo': this.accountNo,
        'provider': this.orgProvider,
        'packageType': this.pType,
        'cardDetails': JSON.stringify(data.object.cardValueList)
      });
    }

    else if (data.statusCode == 0) {
      this.wimaxRechargeError02 = data.note;
    }

    else {
      this.wimaxRechargeError02 = "System Error Contact with Administrator.";
    }
  };


  ionViewDidLoad() {

  }

}



